import React, { useState } from "react";
import feathersClient from "feathersClient";
import styled from "styled-components";

import FormComponent from "components/Form";
import Modelo from "components/Modelo";

const Container = styled(FormComponent)`
    grid-template-columns: 2fr 4fr 2fr [end];

    label:nth-child(4) {
        grid-column-end: span 2;
    }

    input[name="labor"] {
        text-align: right;
    }
`;

const Presupuesto = function ({ edit, unEdit }) {
    const [inputs, setInputs] = useState({
        patente: "",
        km: "",
        motivo: "",
        labor: "",
        fabricanteId: 0,
        fabricante: "",
        modeloId: 0,
        modelo: "",
    });

    const capitalize = (text) => {
        if (typeof text !== "string") return "";
        return text
            .split(" ")
            .map((s) => s.charAt(0).toUpperCase() + s.substring(1))
            .join(" ");
    };

    function validate(inputs) {
        let error = "";
        inputs.modeloId === 0
            ? (error = "Seleccione un modelo")
            : inputs.motivo === ""
            ? (error = "Ingrese el motivo")
            : (error = "");
        return error;
    }

    const handleCreate = (event) => {
        event.preventDefault();
        validate(inputs) === "" &&
            feathersClient
                .service("presupuestos")
                .create({
                    motivo: inputs.motivo,
                    modeloId: inputs.modeloId,
                })
                .then(() => {})
                .catch((error) => {
                    console.error(error);
                });
    };

    const handleInputChange = (event) => {
        event.persist();
        setInputs((inputs) => ({
            ...inputs,
            [event.target.name]:
                event.target.name === "motivo"
                    ? capitalize(event.target.value)
                    : event.target.value,
        }));
    };

    return (
        <Container edit={edit} unEdit={unEdit} onSubmit={handleCreate}>
            <label>
                Patente
                <input
                    type="text"
                    name="patente"
                    placeholder="-"
                    autoComplete="off"
                    value={inputs.patente}
                    onChange={handleInputChange}
                    required
                />
            </label>
            <Modelo
                inputs={inputs}
                setInputs={setInputs}
                onChange={handleInputChange}
            />
            <label>
                KM
                <input
                    type="number"
                    min="0000000"
                    max="9999999"
                    name="km"
                    placeholder={"-"}
                    value={inputs.km}
                    onChange={handleInputChange}
                />
            </label>
            <label>
                Motivo
                <input
                    type="text"
                    name="motivo"
                    placeholder="-"
                    autoComplete="off"
                    value={inputs.motivo}
                    onChange={handleInputChange}
                />
            </label>
            <label>
                Mano de obra
                <input
                    type="number"
                    min="0000000"
                    max="9999999"
                    name="labor"
                    placeholder="$0"
                    value={inputs.labor}
                    onChange={handleInputChange}
                />
            </label>
        </Container>
    );
};

export default Presupuesto;
